<template>
  <v-container
    tag="section"
  >
    <v-card dark>
      <v-row justify="center">
        <v-col
          cols="12"
          sm="8"
        >
          <v-data-table
            v-model="selected"
            dark
            :headers="headers"
            :single-expand="true"
            :items="filteredMesurements"
            :footer-props="{itemsPerPageText: '', itemsPerPageOptions: []}"
            :disable-sort="true"
            single-select
            class="elevation-1"
            @click:row="expandRow1"
          >
            <template v-slot:top>
              <v-toolbar
                flat
                height="100"
              >
                <v-select
                  v-model="search"
                  :items="scales"
                  label="Vaga"
                  class="select"
                  filled
                  clearable
                />
                <v-spacer />
                <v-checkbox
                  v-model="fav"
                  label="sa zvezdicom"
                />
              </v-toolbar>
            </template>
            <template v-slot:item.fav="{ item }">
              <v-simple-checkbox
                v-model="item.fav"
                v-ripple
                off-icon="mdi-star-outline"
                on-icon="mdi-star"
                color="yellow"
              />
            </template>
            <template v-slot:item.thumb="{ item }">
              <v-img
                :height="isMobile ? 300 : 40"
                :width="isMobile ? 300 : 40"
                :src="item.thumb"
                contain
              />
            </template>
          </v-data-table>
        </v-col>

        <v-col
          cols="12"
          sm="4"
        >
          <br>
          <br>
          <br>
          <br>
          <br>
          <h2 v-show="!isMobile">Merenje #{{ s }}</h2>
          <v-simple-table
            v-show="!isMobile"
          >
            <tr>
              <td>Vaga</td>
              <td>{{ mesurments[s].scale }}</td>
            </tr>
            <tr>
              <td>Tezina</td>
              <td>{{ mesurments[s].kg }}</td>
            </tr>
            <tr>
              <td>vreme</td>
              <td>{{ mesurments[s].ts }}</td>
            </tr>
          </v-simple-table>
          <v-img
            v-show="!isMobile"
            :src="kitten"
            max-height="300"
            contain
          />
          <br>
          <v-textarea
            v-show="!isMobile"
            v-model="note"
            solo
            name="input-7-4"
            label="Beleske"
          />
        </v-col>
      </v-row>
    </v-card>
  </v-container>
</template>

<script>
  export default {
    data: () => ({
      kitten: '',
      selected: [],
      s: 0,
      fav: false,
      headers: [],
      mesurments: [
        { id: 1, notes: 'aaa', fav: true, scale: 'vaga_1', kg: 123, ts: '11:22' },
        { id: 2, notes: 'bbb', fav: false, scale: 'vaga_1', kg: 321, ts: '22:33' },
        { id: 3, notes: '', fav: false, scale: 'vaga_1', kg: 333, ts: '33:44' },
        { id: 4, notes: '', fav: false, scale: 'vaga_2', kg: 333, ts: '33:44' },
        { id: 5, notes: '', fav: false, scale: 'vaga_2', kg: 333, ts: '33:44' },
        { id: 6, notes: '', fav: true, scale: 'vaga_2', kg: 333, ts: '33:44' },
        { id: 7, notes: '', fav: false, scale: 'vaga_1', kg: 333, ts: '33:44' },
        { id: 8, notes: '', fav: false, scale: 'vaga_1', kg: 333, ts: '33:44' },
        { id: 9, notes: '', fav: false, scale: 'vaga_1', kg: 333, ts: '33:44' },
        { id: 10, notes: '', fav: false, scale: 'vaga_1', kg: 333, ts: '33:44' },
        { id: 11, notes: '', fav: false, scale: 'vaga_1', kg: 333, ts: '33:44' },
        { id: 12, notes: '', fav: false, scale: 'vaga_3', kg: 333, ts: '33:44' },
        { id: 13, notes: '', fav: false, scale: 'vaga_3', kg: 333, ts: '33:44' },
        { id: 14, notes: '', fav: false, scale: 'vaga_1', kg: 333, ts: '33:44' },
        { id: 15, notes: '', fav: false, scale: 'vaga_1', kg: 333, ts: '33:44' },
        { id: 16, notes: '', fav: false, scale: 'vaga_1', kg: 333, ts: '33:44' },
        { id: 17, notes: '', fav: false, scale: 'vaga_3', kg: 333, ts: '33:44' },
      ],
      scales: ['vaga_1', 'vaga_2', 'vaga_3'],
      search: '',
    }),
    computed: {
      isMobile: function () {
        return screen.width <= 760
      },
      note: {
        get: function () {
          return this.mesurments[this.s].notes
        },
        set: function (v) {
          this.mesurments[this.s].notes = v
        },
      },
      filteredMesurements: function () {
        var m = this.mesurments
        m.map((item, i) => {
          item.thumb = 'http://placekitten.com/20' + item.id + '/30' + item.id
          item.selected = false
          return item
        })
        if (this.fav) {
          m = m.filter(item => {
            return item.fav === this.fav
          })
        }
        if (!this.search) {
          return m
        }
        return m.filter(item => {
          return item.scale === this.search
        })
      },
    },
    mounted () {
      this.expandRow1({ id: 1 }, { index: 0 })
      var ml = this.isMobile ? 'Merenje #' : '#'
      this.headers = [
        { text: '', value: 'fav' },
        { text: '', value: 'thumb', align: 'center' },
        { text: ml, value: 'id' },
        { text: 'vaga', value: 'scale' },
        { text: 'tezina', value: 'kg' },
        { text: 'vreme', value: 'ts' },
      ]
    },
    methods: {
      expandRow: function (x) {
        this.kitten = 'http://placekitten.com/20' + x.item.id + '/30' + x.item.id
      },
      expandRow1: function (x, y) {
        if (this.isMobile) {
          return
        }
        this.s = x.id
        var rows = document.querySelectorAll('table tr')
        rows.forEach(function (e) {
          e.classList.remove('selected')
        })
        rows[this.s].classList.add('selected')
        this.kitten = 'http://placekitten.com/20' + x.id + '/30' + x.id
      },
    },
  }
</script>

<style>
 tr.selected {background: #616161}
 tr {cursor: pointer}
 .select {max-width: 150px}
 td:first-child, td:nth-child(2){
    width:50px;
 }
</style>
